<template>
  <mrc_ess_skLoaders :skLoading='skLoading'>
    <v-card>
      <v-card-subtitle>
        <mrc_ess_iconGroup style="top: -50px"
                           v-show="disabled"
                           v-if="$store.state.uuid"
                           @handleReturn='handleReturn'
                           @handleEdit='handleEdit'
                           @handleDel='handleDel'></mrc_ess_iconGroup>
      </v-card-subtitle>
      <v-card-text>
        <v-row dense>
          <v-col class="d-flex"
                 cols="12"
                 sm="2">
            <v-switch v-model="detailData.status_uuid"
                      :label="detailData.status_uuid?$t('label_To_enable_the'): $t('label_disable')"
                      color="primary"
                      :readonly="disabled"></v-switch>
          </v-col>
        </v-row>

        <v-form ref="add_func">
          <v-row dense>
            <!-- <v-col cols="12"
                   sm="6"
                   class="d-flex">
              <v-text-field :label="$t('label_The_name_of_the_function') + '*'"
                            required
                            v-model="detailData.function_name"
                            append-icon="mdi-longitude"
                            :readonly="disabled"
                            :rules="$text_rules('功能名称')">
              </v-text-field>
            </v-col>
            <v-col cols="12"
                   sm="6"
                   class="d-flex">
              <v-text-field :readonly="disabled"
                            required
                            :label="$t('label_Function_short_code') + '*'"
                            v-model="detailData.function_shortcode"
                            append-icon="mdi-longitude"
                            :rules="$text_rules('功能短编码')">
              </v-text-field>
            </v-col>
            <v-col cols="12"
                   sm="12"
                   class="d-flex">
              <v-text-field :readonly="disabled"
                            required
                            :label="$t('label_Functional_description') + '*'"
                            v-model="detailData.function_description"
                            append-icon="mdi-longitude"
                            :rules="$text_rules('功能描述')">
              </v-text-field>
            </v-col> -->
            <v-col cols="6">
              <mrc_ess_multiText :label="$t('label_The_name_of_the_function') + '*'"
                                 style="width: 100%"
                                 required
                                 :isNational="judegNational('function_name')"
                                 v-model="detailData.function_name"
                                 append-icon="mdi-longitude"
                                 :readonly="disabled"
                                 :rules="$text_rules($t('label_The_name_of_the_function'))">
              </mrc_ess_multiText>
            </v-col>
            <v-col cols="12"
                   sm="6">
              <mrc_ess_multiText :readonly="disabled"
                                 style="width: 100%"
                                 required
                                 :label="$t('label_Function_short_code') + '*'"
                                 :isNational="judegNational('function_shortcode')"
                                 v-model="detailData.function_shortcode"
                                 append-icon="mdi-longitude"
                                 :rules="$text_rules($t('label_Function_short_code'))">
              </mrc_ess_multiText>
            </v-col>
            <v-col cols="12"
                   sm="12"
                   class="d-flex">
              <mrc_ess_multiText :readonly="disabled"
                                 style="width: 100%"
                                 required
                                 :label="$t('label_Functional_description') + '*'"
                                 :isNational="judegNational('function_description')"
                                 v-model="detailData.function_description"
                                 :rules="$text_rules($t('label_Functional_description'))">
              </mrc_ess_multiText>
            </v-col>
            <v-col cols="12"
                   sm="6"
                   class="d-flex">

              <v-select :items="top_module"
                        :label="$t('label_The_superior_module') + '*'"
                        :isNational="judegNational('module_uuid')"
                        v-model="detailData.module_uuid"
                        item-text='view_fields'
                        item-value='value_field'
                        @change="moduleChange"
                        :readonly="Boolean($store.state.uuid)"
                        required
                        chips
                        :rules="[v => !!v || $t('label_The_superior_module')]"></v-select>
            </v-col>
            <v-col cols="12"
                   sm="6">
              <v-select :items="version_arr"
                        :label="$t('label_version') + '*'"
                        required
                        v-if="!$store.state.uuid"
                        :rules="[v=>!!v || $t('label_version')]"
                        :readonly="Boolean($store.state.uuid)"
                        v-model="detailData.version"
                        chips></v-select>
              <v-text-field v-else
                            disabled
                            :label="$t('label_version') + '*'"
                            v-model="detailData.version"
                            :rules="$text_rules($t('label_version'))">
              </v-text-field>
            </v-col>
            <v-col cols="12"
                   sm="12"
                   class="d-flex">
              <v-select :readonly="disabled"
                        :items="data_table"
                        :label="$t('label_Associated_data_table') + '*'"
                        v-model="detailData.table_uuid"
                        item-text='view_fields'
                        item-value='value_field'
                        required
                        chips
                        :rules="[v => !!v || $t('label_Associated_data_table')]"></v-select>
            </v-col>
            <v-col cols="12"
                   sm="6">
              <v-select :readonly="disabled"
                        :label="$t('label_Client_type') + '*'"
                        v-model="detailData.client_type_uuid"
                        :items="client_type"
                        item-value="value_field"
                        item-text="view_fields"
                        required
                        no-data-text="请先选择上级模块"
                        chips
                        multiple
                        :rules="[v => v.length>0 || $t('label.client_type')]"></v-select>
            </v-col>
            <v-col cols="12"
                   sm="6"
                   class="d-flex">
              <v-select :readonly="disabled"
                        :items="func_type"
                        :label="$t('label_Function_type') + '*'"
                        v-model="detailData.function_type_uuid"
                        item-text='dict_label'
                        item-value='dict_value'
                        required
                        chips
                        :rules="[v => !!v || $t('label_Function_type')]"></v-select>
            </v-col>

            <v-col cols="12"
                   sm="12"
                   class="d-flex">
              <v-select :readonly="disabled"
                        :items="unit_data"
                        :label="$t('label_Only_for_the_following_units')"
                        v-model="detailData.org_level_uuid"
                        item-value='value_field'
                        item-text='view_fields'
                        multiple
                        chips></v-select>
              <!-- :rules="[v => v.length > 0 || '特别只放开给以下单位']" -->
            </v-col>
          </v-row>
        </v-form>
      </v-card-text>
      <mrc_ess_submitBtn v-if="!disabled"
                         :cancelText="$t('label_cancel')"
                         :successText='successText'
                         @cancel='cancel'
                         @success='success'></mrc_ess_submitBtn>
    </v-card>
  </mrc_ess_skLoaders>

</template>

<script>
import { getBusinessList, get_func_add, get_versions, get_func_del } from '@/service/api/func_module'
import { getBusinessList as getprompt } from '@/service/api/prompt'
import { getDataByName } from '@/service/api/data_dictionary'
// import iconGroup from '@/components/iconGroup'
export default {
  components: {},
  props: {
    func_data: {
      type: Object
    },
    func_disabled: {
      type: Boolean
    }
  },
  data () {
    return {
      skLoading: true,
      successText: this.$t('label_The_next_step'),
      detailData: {
        function_name: '',
        function_shortcode: '',
        function_description: '',
        table_uuid: '',
        function_type_uuid: '',
        module_uuid: '',
        org_level_uuid: [],// 多选
        status_uuid: true,
        client_type_uuid: [],
        version: '',
        uuid: null
      },
      uuid: null,
      top_module: [],
      data_table: [],
      func_type: [],
      unit_data: [],
      version_arr: [],
      client_type: [],
      version_uuid: '',
      disabled: false,
      tempData: {}
    };
  },
  created () {
  },
  async mounted () {
    this.skLoading = true
    // 上级模块
    try {
      // if (!this.func_disabled && !this.$store.state.iconstatus) {
      //   this.disabled = true
      // }
      await this.national('mrbase_sys_functions')
      const top_module = await getprompt({
        prompt_name: 'function_parent',
        'uuid': this.detailData.uuid
      })
      this.top_module = top_module.data
      // 关联数据表
      const data_table = await getBusinessList('table_name')
      this.data_table = data_table.data
      // func_type 功能类型
      // 查询功能类型
      const func_type = await getDataByName('functionType')
      this.func_type = func_type.data
      // 开放以下单位
      const unit_data = await getBusinessList('open_util')
      this.unit_data = unit_data.data
      // 新增的时候进页面就不传上级模块 获取顶级模块的版本
      // 修改的时候传上级模块 获取版本
      const version_data = await get_versions({ module_uuid: this.detailData.module_uuid })
      this.version_arr = version_data.data.version.split(',')
      this.version_uuid = version_data.data.uuid
      this.skLoading = false
    } catch (error) {
      this.skLoading = false
    }
  },
  methods: {
    // 新增的时候 上级模块改变  要去改版本
    // 修改的时候 上级模块改变 不修改版本

    // 功能客户端类型根据上级模块来变化
    async moduleChange (val) {
      // 如果是新增就可以修改版本
      if (!this.$store.state.uuid) {
        this.detailData.version = ''
        this.version_arr = []
        const version_data = await get_versions({ module_uuid: val })
        this.version_arr = version_data.data.version.split(',')
        this.version_uuid = version_data.data.uuid
      }
      // 功能客户端类型  高级参照
      const client_type = await getprompt({
        prompt_name: 'function_client_type',
        module_uuid: val
      })
      this.client_type = client_type.data
      // console.log(this.client_type,1111111)


    },
    handleReturn () {
      this.$router.go(-1)
    },
    handleEdit () {
      this.disabled = !this.disabled
    },
    // 删除功能
    async handleDel () {
      this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') })
        .then(async () => {
          const del = await get_func_del({ uuid: this.detailData.uuid })
          console.log(del)
          if (del.code == 200) {
            this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
            this.$router.push({
              name: 'module_func_manage'
            })
          }
        }).catch(err => { })
    },
    // 取消
    cancel () {
      this.$myConfirm({ content: this.$t('message_Confirm_the_cancellation_of_the_edit') })
        .then(() => {
          if (this.detailData.uuid) {
            this.disabled = !this.disabled
            this.$emit('func_cancel')
          } else {
            this.$router.go(-1)
          }
        }).catch(() => { })

      // 取消的时候显示修改前的值
    },
    // 确认
    success () {
      const isSubmit = this.$refs.add_func.validate()
      if (isSubmit) {
        const obj = this.$clone(this.detailData)
        obj.status_uuid = obj.status_uuid ? 1 : 0
        obj.org_level_uuid = obj.org_level_uuid.join()
        obj.client_type_uuid = obj.client_type_uuid.join()
        if (obj.created_utc_datetime) {
          delete obj.created_utc_datetime
          delete obj.updated_utc_datetime
        }
        // 修改的值传出去
        this.$emit('getData', obj)
        // 把选择了的客户端类型传给按钮做下拉
        let client_type = []
        this.client_type.forEach(item => {
          this.detailData.client_type_uuid.forEach(items => {
            if (item.value_field == items) {
              client_type.push(item)
            }
          })
        })
        this.$emit('getBtnClientType', client_type)
        this.$emit('getVersion_uuid', this.version_uuid)
      }
    },
  },
  computed: {

  },
  watch: {
    func_data: {
      handler (val) {
        this.$nextTick(async () => {
          this.disabled = true
          this.successText = this.$t('label_submit')
          const obj = this.$clone(val)
          if (obj.client_type_uuid) {
            obj.client_type_uuid = obj.client_type_uuid.split(',')
          }
          await this.moduleChange(obj.module_uuid)
          this.detailData = obj
          this.tempData = obj
          // 修改数据的时候把客户端类型，给按钮使用
          let client_type = []
          this.client_type.length > 0 && this.client_type.forEach(item => {
            obj.client_type_uuid.forEach(items => {
              if (item.value_field == items) {
                client_type.push(item)
              }
            })
          })
          this.$emit('getBtnClientType', client_type)
        })
      },
      deep: true
    },
    func_disabled () {
      this.disabled = !this.disabled
      this.tempData = this.detailData
    }
  },
};
</script>

<style scoped>
</style>
